<template>
  <div class="hot animate__animated animate__fadeIn">
    <div class="hottop">
      <div class="font">
        <div class="img"></div>
        <p>更新日期：2月18日</p>
      </div>
    </div>
    <div class="hotcont">
      <LeftNumberList
        :NewMusicList="NewMusicList"
        :MusicId="$attrs.MusicId"
        :paused='$attrs.paused'
        @updata:music="$emit('updata:music', $event)"
        @updata:playlist="$emit('updata:playlist', $event)"
      ></LeftNumberList>
    </div>
    <div class="foot">
      <img
        src="data:image/gif;base64,"
      />
      <span>加载更多 ></span>
    </div>
  </div>
</template>

<script>
import LeftNumberList from "@/components/LeftNumberList.vue";

export default {
  components: {
    LeftNumberList,
  },
  data: function () {
    return {
      NewMusicList: [],
    };
  },
  created() {
    this.axios.get("/top/list?idx=1").then((datas) => {
      var playhotlist = datas.data.playlist.trackIds
        .slice(0, 20)
        .map((item) => item.id)
        .join();
      this.axios.get("/song/detail?ids=" + playhotlist).then((datas) => {
        this.NewMusicList = datas.data.songs;
        console.log(this.NewMusicList);
      });
    });
  },
};
</script>

